<template>
    <div class="airport-search">
        <input type="search" autocomplete="off" :class="extendedOptions.inputClass"
               v-bind="$attrs"
               v-on:keydown="onKeyDown"
               v-on:blur="hideItems"
               v-on:focus="showItems = true"
               v-model="query"
               :id="extendedOptions.id"
               :placeholder="extendedOptions.placeholder" :required="extendedOptions.required" >
        <div class="airports-list">
            <ul class="items" v-show="items.length > 0 && showItems === true">
                <li class="item"
                    :key="index"
                    v-for="(item, index) in items"
                    @click.prevent="selectItem(index)"
                    v-bind:class="{ 'is-active': index === activeItemIndex }">
                    <slot name="item" :item="item">
                        <span class="name">(<span class="code">{{item.iata}}</span>) {{item.name}}</span>
                    </slot>
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
    import debounce from 'debounce'

    export default {
        name: "AirportSearch",
        inheritAttributes: true,
        props: {
            options: {
                type: Object,
                default: null
            },
            onInputChange: {
                type: Function,
                required: true
            },
            onItemSelected: {
                type: Function
            },
            value: {
                type: String,
                required: true
            }
        },
        data () {
            const defaultOptions = {
                debounce: 0,
                placeholder: '',
                inputClass: 'form-control'
            };
            const extendedOptions = Object.assign({}, defaultOptions, this.options);
            return {
                extendedOptions,
                query: this.value,
                lastSetQuery: null,
                items: [],
                activeItemIndex: -1,
                showItems: false
            }
        },
        beforeMount () {
            if (this.extendedOptions.debounce !== 0) {
                this.onQueryChanged = debounce(this.onQueryChanged, this.extendedOptions.debounce);
            }
        },
        watch: {
            'query': function (newValue, oldValue) {
                if (newValue === this.lastSetQuery) {
                    this.lastSetQuery = null;
                    return
                }
                this.onQueryChanged(newValue);
                this.$emit('input', newValue)
            },
            'value': function (newValue, oldValue) {
                this.setInputQuery(newValue)
            }
        },
        methods: {
            onKeyDown (e) {
                //C1
                switch (e.keyCode) {
                    case 40:
                        this.highlightItem('down');
                        e.preventDefault();
                        break;
                    case 38:
                        this.highlightItem('up');
                        e.preventDefault();
                        break;
                    case 13:
                        this.selectItem();
                        e.preventDefault();
                        break;
                    case 27:
                        this.showItems = false;
                        e.preventDefault();
                        break;
                    default:
                        return true
                }
            },
            onQueryChanged (value) {
                //C2
                const result = this.onInputChange(value);
                this.items = [];
                if (typeof result === 'undefined' || typeof result === 'boolean' || result === null) {
                    return
                }
                if (result instanceof Array) {
                    this.setItems(result)
                } else if (typeof result.then === 'function') {
                    result.then(items => {
                        this.setItems(items)
                    })
                }
            },
            setInputQuery (value) {
                //C3
                this.lastSetQuery = value;
                this.query = value;
            },
            setItems (items) {
                //C4
                if(items === null || items === undefined){
                    return;
                }
                this.items = items;
                this.activeItemIndex = -1;
                this.showItems = true
            },
            highlightItem (direction) {
                //C5
                if (this.items.length === 0) {
                    return;
                }
                let selectedIndex = this.items.findIndex((item, index) => {
                    return index === this.activeItemIndex;
                });
                if (selectedIndex === -1) {
                    // nothing selected
                    if (direction === 'down') {
                        selectedIndex = 0;
                    } else {
                        selectedIndex = this.items.length - 1;
                    }
                } else {
                    this.activeIndexItem = 0;
                    if (direction === 'down') {
                        selectedIndex++;
                        if (selectedIndex === this.items.length) {
                            selectedIndex = 0;
                        }
                    } else {
                        selectedIndex--;
                        if (selectedIndex < 0) {
                            selectedIndex = this.items.length - 1;
                        }
                    }
                }
                this.activeItemIndex = selectedIndex
            },
            selectItem (index) {
                //C6
                let item = null;
                if (typeof index === 'undefined') {
                    if (this.activeItemIndex === -1) {
                        return;
                    }
                    item = this.items[this.activeItemIndex];
                } else {
                    item = this.items[index];
                }
                if (!item) {
                    return;
                }
                if (this.onItemSelected) {
                    this.onItemSelected(item);
                } else {
                    this.onItemSelectedDefault(item);
                }
                this.hideItems();
            },
            hideItems () {
                //C7
                setTimeout(() => {
                    this.showItems = false;
                }, 300)
            },
            onItemSelectedDefault (item) {
                //C8
                if (typeof item === 'string') {
                    this.$emit('input', item);
                    this.setInputQuery(item);
                    this.showItems = false;
                }
            },
            showResults () {
                //C9
                this.showItems = true;
            },
        }
    }
</script>
<style lang="scss" scoped>
    @import "../scss/AirportSearch.scss";
</style>
