import axios from "axios";
import * as Util from '../api/utils.js';

export const apiBaseURL = 'http://localhost:5000';
const appBaseURL = process.env.NODE_ENV === 'production' ? '/star-travel/' : '/';
//#region external API calls
export async function searchFlights(params) {
    let reqData = {
        "originLocation": params.originLocation,
        "destLocation": params.destLocation,
        "departureDateTime": params.departureDateTime,
        "returnDateTime": params.returnDateTime,
        "directFlights": params.directFlights,
        "adults": params.adults,
        "childs": params.childs,
        "infants": params.infants,
        "tripType": params.tripType,
        "cabinType": params.cabinType,
    };
    let respData = await axios.post(apiBaseURL + "/api/flight/searchflights", reqData).then((response) => {
        // Handle success
        if (response.data.data.airItinerary.length > 0) {
            //console.log("1 data received in searchFlights");
            return response.data.data.airItinerary;
        }
    }).catch((error) => {
        console.error("An error occurred.");
        console.error(error);
        return []
    }).finally(() => {
        //Perform action in always
    });
    //console.log("2 return from searchFlights");
    return respData;
}

export async function getAirports(query) {
    let queryData = await axios
        .get(apiBaseURL + "/getlocation/" + query)
        .then((result) => {
            return result.data.data;
        })
        .catch((error) => {
            console.error("An error occurred.");
            console.error(error);
            return [];
        }).finally(() => {
            //Perform action in always
        });
    return queryData;
}

export async function getLocalFlightsData(params) {
    let reqData = {
        "originLocation": params.originLocation,
        "destLocation": params.destLocation,
        "departureDateTime": params.departureDateTime,
        "returnDepartureDateTime": params.returnDateTime,
        "directFlights": params.directFlights,
        "adults": params.adults,
        "childs": params.childs,
        "infants": params.infants,
        "tripType": params.tripType,
        "cabinType": params.cabinType,
    };

    let respData = await axios.get( `${appBaseURL}meta-data/response_1608444076416.json`).then((response) => {
        // Handle success
        //console.log(response.data.groupedItineraryResponse);
        return response.data.groupedItineraryResponse;
    }).catch((error) => {
        console.error("An error occurred.");
        console.error(error);
        return [];
    }).finally(() => {
        //Perform action in always
    });
    //console.log("2 return from getLocalFlightsData");
    return respData;
}

//#endregion

