<template>
    <div id="appWrapper">
        <div class="preloader" v-if="loading"></div>
        <AppHeader></AppHeader>
        <main>
            <transition name="fade" mode="out-in">
                <keep-alive>
                    <router-view></router-view>
                </keep-alive>
            </transition>
        </main>
        <AppFooter></AppFooter>
    </div>
</template>
<script>
    import AppHeader from "./views/common/Header.vue";
    import AppFooter from "./views/common/Footer.vue";
    import * as DAO from "./api/apiCalls";

    export default {
        name: 'APP',
        components: {
            AppHeader,
            AppFooter
        },
        data() {
            return {
                loading: true
            }
        },
        methods: {
            bindEvents() {
                if (typeof (BindCustomLibraryEvents) == "function") {
                    // eslint-disable-next-line no-undef
                    BindCustomLibraryEvents();
                }
            }
        },
        //life cycle hooks
        beforeCreate() {
            //console.log("beforeCreate called 1");
        },
        created() {
            //console.log("created called 2");
        },
        beforeMount() {
            //console.log("beforeMount called 3");
            DAO.getAirportsData().then(response => {
                this.$store.commit('setAirports', response);
            });
        },
        mounted() {
            //this.bindEvents();
            //console.log("mounted called 4");
            let _this = this;
            setTimeout(function () {
                _this.loading = false;
            }, 1000);
        },
        beforeUpdate() {
            //console.log("beforeUpdate called 5");
        },
        updated() {
            //console.log("updated called 6");
            this.bindEvents();
        },
        activated() {
            //this.bindEvents();
            //console.log("activated called 7");
        },
        deactivated() {
            //console.log("deactivated called 8");
        },
        beforeUnmount() {
            //console.log("beforeUnmount called 9");
        },
        unmounted() {
            //console.log("unmounted called 10");
        },
        errorCaptured() {
            //console.log("errorCaptured called 11");
        },
        renderTracked() {
            //console.log("renderTracked called 12");
        },
        renderTriggered() {
            //console.log("renderTriggered called 13");
        },
    }
</script>
<style lang="scss">
    @import "scss/styles.scss";
</style>
